import { defineStore } from "pinia";

const useStore = defineStore("userData", {
  state: () => {
    return {
      // test
      counter: 0,
      // 伴奏文件
      banzou: null,
      // 歌词编码
      lrcEncoding: "GBK",
      // 歌词file
      lrc: null,
      // lrc文字内容
      lrcText: "",
      //保存带有时间戳的歌词
      parsedLyrics: [],
      // 歌词延迟时间
      lrcDelayTime: 0,
      // 伴奏的时长
      banzouLength: 0,
      // 录的时长 0就是和伴奏一样长, >0就是自定义时长
      recordLength: 0,
      // 伴奏的音量
      banzouVolume: 1,
      // 升降调
      banzouPitch: 0,
      // 录音的二进制数据
      audioChunk: [],
      // 混响效果
      reverb: false,
      // 人声迟延
      voiceDelay: 0,
      //输出的时候是否压缩成mp3
      compress: false,
      // 加载中
      loading: false,
      // 音频生成中
      rendering: false,
      // 音频生成进度
      renderingProgess: 0,
      //全局alert
      alertDialog: {
        show: false,
        text: "",
      },
      // 混响效果
      reverbOption: {
        time: 1.8,
        decay: 1.5,
        mix: 0.4
      },
      // 混响默认效果 恢复默认的时候使用 不要修改它!!!
      reverbOption_default: {
        time: 1.8,
        decay: 1.5,
        mix: 0.4
      },      
    };
  },
  actions: {
    parseLRC() {
      //console.log("开始解析歌词 " + this.lrcText);
      let lrcContent = this.lrcText;
      if (!lrcContent) {
        console.log("歌词内容为空");
        return;
      }
      const lines = lrcContent.split(/[\r\n]/);
      const timeExp = /\[(\d{2}):(\d{2})\.(\d{2,3})\]/; // 匹配时间戳 [mm:ss.xx]
      const lyricsList = [];

      lines.forEach((line) => {
        const match = timeExp.exec(line);
        if (match) {
          const minutes = parseInt(match[1], 10);
          const seconds = parseInt(match[2], 10);
          let milliseconds = parseInt(match[3], 10);
          milliseconds = match[3].length > 2 ? parseInt(match[3], 10) / 1000 : parseInt(match[3], 10) / 100;
          const timeInSeconds = minutes * 60 + seconds + milliseconds;

          const lyricText = line.replace(timeExp, "").trim();

          // 将歌词转换为Picker需要的格式  这个time和value貌似留一个就行,但是这个是ai写的,去掉也不知道哪会有问题,所以就先这样吧...
          lyricsList.push({
            text: lyricText,
            value: timeInSeconds + this.lrcDelayTime,
            time: timeInSeconds + this.lrcDelayTime,
          });
        }
      });

      this.parsedLyrics = lyricsList;
    },
    // 读取歌词
    readLrcText() {
      return this.readText().then((txt) => {
        //console.log("读取歌词成功 " + txt);
        this.lrcText = txt;
        this.parseLRC();
      });
    },

    readText() {
      if (!this.lrc) {
        console.log("没有歌词文件");
        return;
      }
      return new Promise((rs) => {
        let fr = new FileReader();
        fr.onload = (e) => {
          rs(e.target.result);
        };
        fr.readAsText(this.lrc, this.lrcEncoding);
      });
    },

    // test
    increment() {
      this.counter++;
    },

    // 显示loading
    showLoading() {
      this.loading = true;
    },

    hideLoading() {
      this.loading = false;
    },
    // 显示render progess
    showProgess(n) {
      this.rendering = true;
      this.renderingProgess = n;
    },

    hideProgess() {
      this.rendering = false;
      setTimeout(() => {
        this.renderingProgess = 0;
      }, 1000);
    },
    showAlert(txt) {
      this.alertDialog.show = true;
      this.alertDialog.text = txt;
    },

    hideAlert() {
      this.alertDialog.show = false;
      this.alertDialog.text = "";
    },
  },
  // 持久化
  persist: {
    paths: ["lrcDelayTime","lrcEncoding","compress", "reverb", "reverbOption", "voiceDelay", "banzouVolume"],
  },
});

export default useStore;
